<template>
  <div class="choose-language">
    <h2>Wybierz jzyk</h2>
    <div class="controls">
      <input type="text" placeholder="Filtruj" v-model="searchText">
      <button @click="selectRandom">Wylosuj</button>
    </div>
    <div class="languages-list-container">
      <transition>
      <div class="languages-list-loading" v-if="!languagesList">Loading</div>
      </transition>
      <div class="languages-list">
        <label :class="{'highlight':selected == index}" v-for="(language, index) in filteredList" :key="language.name" :id="index">
          <input type="radio" name="language-radio" :value="index" @input="setLanguage"/>
          <span class="language-name">{{ language.name.replace('_', ' ') }}</span>
        </label>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';

export default {
  data() {
    return {
      selected: null,
      searchText: '',
    };
  },
  computed: {
    ...mapGetters(['languagesList']),
    filteredList() {
      return this.languagesList.filter((language) => language.name.toLowerCase().includes(this.searchText.toLowerCase()));
    },
  },
  methods: {
    setLanguage(e) {
      this.selected = e.target.value;
      this.$store.commit('SET_LANGUAGE', { ...this.filteredList[this.selected], index: this.selected });
    },
    selectRandom() {
      const index = Math.floor(Math.random() * this.filteredList.length);
      if (index === this.selected && this.filteredList.length > 1) {
        this.selectRandom();
      } else {
        this.setLanguage({ target: { value: index } });
      }
    },
  },
  created() {
    this.$store.dispatch('loadLanguagesList');
  },
};
</script>

<style scoped>
.languages-list {
  padding: .5em 0;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: .5em;
}

input[type="radio"] {
  display: none;
}

label {
  border: 2px solid;
  border-radius: 5px;
  border-image: linear-gradient(30deg, var(--accent1), var(--accent2)) 1;
  padding: 1em;
  text-align: center;
}

label:hover {
  background: linear-gradient(30deg, rgba(145, 28, 213, .4), rgba(96, 67, 253, .4));
}

label.highlight {
  background: linear-gradient(30deg, var(--accent1), var(--accent2));
}

</style>
