<template>
  <div class="start">

    <SettingsMenu ref="settings"/>

    <label>Wrzu swj kod
      <input type="checkbox" v-model="customCode">
    </label>
    <div class="uploadCode" v-if="customCode">
      <UploadCode />
    </div>

    <LanguagesList v-else/>


    <button v-if="!room.name" @click="run" :disabled="!language.name">START</button>
    <button v-else @click="createRoom" :disabled="!language.name">Zastosuj dla pokoju</button>
    <p v-if="error">{{ error }}</p>

    <aside v-if="room.players.length">
      <PlayersList />
    </aside>

  </div>
</template>

<script>
import SettingsMenu from '@/components/SettingsMenu.vue';
import LanguagesList from '@/components/LanguagesList.vue';
import UploadCode from '@/components/UploadCode.vue';
import PlayersList from '@/components/PlayersList.vue';
import { mapGetters } from 'vuex';
import axios from 'axios';

export default {
  name: 'Start',
  data() {
    return {
      customCode: false,
      error: '',
    };
  },
  components: {
    SettingsMenu,
    LanguagesList,
    UploadCode,
    PlayersList,
  },
  computed: {
    ...mapGetters(['room', 'language', 'options', 'code']),
  },
  watch: {
    customCode() {
      if (this.customCode === false) {
        this.$store.dispatch('deleteCustomCode');
      }
    },
  },
  methods: {
    run() {
      console.log('run');
      if (this.customCode) {
        if (this.code.text.length < 30 || this.code.lines < 5) {
          this.error = 'Co taki krtki?';
          return;
        }
      }

      this.$refs.settings.commitSettings();
      this.$router.push('run');
    },
    createRoom() {
      this.$refs.settings.commitSettings();
      const url = `${window.location.origin}/createroom`;
      const data = {
        name: this.room.name,
        players: this.room.players,
        options: this.options,
        language: this.language,
        fileName: Math.floor(Math.random() * this.language.files.length),
        customCode: this.code,
      };

      axios.post(url, data);
    },
  },
};
</script>

<style scoped>
aside {
  position: absolute;
  top: 5em;
  right: 5em;
}
</style>
