<template>
  <div>
    <h1>{{ format(WPM, 0, 1) }} WPM = {{ format(CPM, 0, 1) }} CPM</h1>
    <h2>{{ stats.codeLength }} poprawnych w {{ format(stats.timeFromFirstInput, 1) }} s</h2>
    <h3>Prdko pocztkowa: {{ format(oneThirdWPM, 0, 1) }} WPM</h3>
    <h3>Prdko kocowa: {{ format(lastThirdWPM, 0, 1) }} WPM</h3>
    <h4>Czas reakcji po starcie (nie wliczany): {{ format(longestTimeOfCorrection, 0, 1) }} ms</h4>
    <h4>Odstpy midzy klikniciami: {{ format(keyPressAvgInterval, 0, 1) }} ms</h4>
    <div class="mistakes" v-if="mistakes.length">
      <h2>Wszystkich bdw: {{ mistakes.length }} / {{ stats.history.length - 1 }} ({{ format(mistakesToClicksRatio, 1, 100) }} %)</h2>
      <ul>
        <li>Czas stracony przez bedy: {{ format(totalTimeLost) }} s</li>
        <li>Prdko odliczajc ten czas: {{ format(WPMWithoutTimeLost, 0, 1) }} WPM</li>
        <li>Najwicej bdw pod rzd: {{ mostMistakesInARow }} znakw</li>
        <li>Najduszy czas poprawki: {{ format(longestTimeOfCorrection) }} s</li>
      </ul>
          <div class="most-frequent">
      <table>
        <thead>
          <tr>
            <td>L.</td>
            <td>Waciwy</td>
            <td>Mylony z</td>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(entry, index) in tableData" :key="index">
            <td>{{ entry.number}}</td>
            <td>{{ entry.expectedText }}</td>
            <td>{{ entry.commonMistakes.join(', ') }}</td>
          </tr>
        </tbody>
      </table>
    </div>
    </div>
    <h2 v-else>Nie popenie adnych bdw.</h2>


  </div>
</template>

<script>
function log(what) {
  try {
    console.log(JSON.parse(JSON.stringify(what)));
  } catch {
    console.log(what);
  }
}
export default {
  name: 'StatsCard',
  props: {
    stats: {
      type: Object,
      required: true,
    },
  },
  data() {
    return {
      mostFrequent: [],
    };
  },
  computed: {
    CPM() {
      return this.stats.codeLength / this.format(this.stats.timeFromFirstInput, 4) * 60;
    },
    WPM() {
      return this.CPM / 5;
    },
    oneThirdWPM() {
      return this.stats.oneThirdCharsCount / this.format(this.stats.oneThirdTime, 4) * 60 / 5;
    },
    lastThirdTime() {
      return this.stats.timeFromFirstInput - this.stats.lastThirdStartTime;
    },
    lastThirdWPM() {
      return this.stats.lastThirdCharsCount / this.format(this.lastThirdTime, 4) * 60 / 5;
    },
    history() {
      return this.stats.history;
    },
    mistakes() {
      return this.history.filter((change) => change.type === 'mistake');
    },
    mistakesToClicksRatio() {
      return this.mistakes.length / (this.history.length - 1);
    },
    mostMistakesInARow() {
      return this.mistakes.map((obj) => obj.fixQueuePos)
        .reduce((acc, value) => Math.max(acc, value), 0);
    },
    correctionTimes() {
      const timesAcc = [];
      for (let i = 1; i < this.history.length; i += 1) {
        // / log(this.history[i]);
        if (this.history[i].type === 'mistake') {
          const startTime = this.history[i].time;
          log(`StartTime ${startTime}; Expected '${this.history[i].expectedText}'`);
          // i+1 must be backspace or another mistake
          for (let j = i + 2; j < this.history.length; j += 1) {
            log(`Looking for correction: ${this.history[j].type}`);
            if (this.history[j].type === 'correct') {
              log(this.history[j].time - startTime);
              timesAcc.push(this.history[j].time - startTime);
              i = j;
              break;
            }
          }
        }
      }
      return timesAcc;
    },
    totalTimeLost() {
      return this.correctionTimes.reduce((acc, value) => acc + value, 0);
    },
    WPMWithoutTimeLost() {
      return this.stats.codeLength / this.format(this.stats.timeFromFirstInput - this.totalTimeLost, 4) * 60 / 5;
    },
    longestTimeOfCorrection() {
      return this.correctionTimes.reduce((acc, value) => Math.max(acc, value), 0);
    },
    startReactionTime() {
      return this.stats.firstCharTime - this.stats.startTime;
    },
    keyPressAvgInterval() {
      return this.stats.codeLength / this.stats.timeFromFirstInput;
    },
    tableData() {
      return this.generateTableData(this.mistakes);
    },
  },
  methods: {
    generateTableData(srcArr) {
      log(srcArr);
      const occur = srcArr
        .reduce((acc, e) => acc.set(e.expectedText, (acc.get(e.expectedText) || 0) + 1), new Map());
      console.info(occur);
      const sorted = [...occur].sort((a, b) => b[1] - a[1]).slice(0, 10).map((occurance) => ({
        expectedText: occurance[0],
        number: occurance[1],
      }));
      console.info(sorted);
      return sorted.map(({ expectedText, number }) => {
        console.log(`expected: ${expectedText}`);
        const filtered = srcArr.filter((obj) => obj.expectedText === expectedText);
        const missClicked = [];
        filtered.forEach((obj) => {
          const char = obj.keyCode.match(/\d/) ? obj.keyCode.match(/\d/)[0][0] : obj.text.toLowerCase().replace(' ', 'Spacja');
          missClicked.push(`${obj.shift ? 'Shift+' : ''}${obj.alt ? 'Alt+' : ''}${obj.shit ? char.replace('"', '\'') : char}`);
        });
        const commonMistakes = [...missClicked
          .reduce((acc, e) => acc.set(e, (acc.get(e) || 0) + 1), new Map())]
          .sort((a, b) => b[1] - a[1])
          .slice(0, 4)
          .map((occurance) => occurance[0]);
        console.log(commonMistakes);
        const temp = {
          expectedText: expectedText.replace(' ', 'Spacja'),
          commonMistakes,
          number,
        };
        return temp;
      });
    },
    format(number, precision = 2, scaler = 0.001) {
      return Math.round(number * scaler * (10 ** precision)) / (10 ** precision);
    },
  },
};
</script>

<style scoped>
table {
  border-collapse: separate;
  border-spacing: 2px 4px;
}
td {
  width: 100px;
  text-align: center;
}

</style>
