import axios from 'axios';

const state = {
  room: {
    name: '',
    players: [],
  },
};

const getters = {
  room: (state) => state.room,
};

const actions = {
  loadRoomData(context, name) {
    console.log(name);
    axios.get(`/api/room/${name}`)
      .then((res) => {
        console.log(res.data);
        context.commit('LOAD_ROOM', res.data);
      });
  },
};

const mutations = {
  SET_ROOM_PROPERTY: (state, payload) => {
    // eslint-disable-next-line prefer-destructuring
    state.room[payload[0]] = payload[1];
  },
  LOAD_ROOM: (state, obj) => {
    state.room = obj;
  },
};

export default {
  state,
  getters,
  actions,
  mutations,
};
