<template>
  <div class="container">
    <div class="countdown">
      <h2> {{ countdownText }} </h2>
    </div>
    <button @click="$emit('reset')">Resetuj</button>
    <div class="code" :class="{ready: editorsReady, completed: isCompleted, heatMap: heatMapReady}">
      <codemirror
      v-model="originalCode"
      :options="cmOptions"
      @ready="onOriginalCmReady"
      id="original"
      ></codemirror>

      <codemirror
      v-model="editorCode"
      :options="cmOptions"
      @ready="onEditorCmReady"
      @changes="onCmCodeChange"
      @beforeChange="onBeforeChange"
      id="editor"
      ></codemirror>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';
import axios from 'axios';
import { codemirror } from 'vue-codemirror';

import { loadMode, loadTheme } from '@/cmLoader';

import 'codemirror/lib/codemirror.css';
// import 'codemirror/addon/edit/closebrackets';
// import 'codemirror/addon/selection/active-line';

// eslint-disable-next-line no-unused-vars
import stats from '../stats';

export default {
  props: {
    fileInfo: {
      type: Object,
      required: true,
    },
  },
  data() {
    return {
      countdown: 3,
      countdownText: 3,
      originalCm: {},
      originalCmDoc: {},
      editorCm: {},
      editorCmDoc: {},
      originalCode: '',
      editorCode: '',
      started: false,
      isCompleted: false,
      editorsReady: false,
      heatMapReady: false,
      toFix: 0,
      markers: [],
      currentLineNr: 0,
      currentChange: {},
      stats: {
        history: [],
        cheat: false,
        firstCharTime: 0,
      },
    };
  },
  components: {
    codemirror,
  },
  computed: {
    ...mapGetters(['language', 'options', 'code']),
    cmOptions() {
      return {
        viewportMargin: Infinity,
        undoDepth: 0,
        tabSize: this.tabWidth,
        styleActiveLine: false,
        lineNumbers: this.options.cm.lineNumbers,
        styleSelectedText: false,
        lineWrapping: true,
        matchBrackets: false,
        dragDrop: false,
        autoCloseBrackets: false,
        cursorBlinkRate: 320,
        smartIndent: false,
        lint: false,
        spellcheck: false,
        autocorrect: false,
        showCursorWhenSelecting: true,
        theme: this.options.cm.theme,
        extraKeys: {
          Backspace: () => {
            console.log(`backspace: ${this.toFix}`);
            if (this.toFix > 0) {
              this.editorCm.execCommand('delCharBefore');
              this.toFix -= 1;
            }
          },
          Tab: () => this.editorCm.execCommand('insertSoftTab'),
          Enter: () => {
            console.time('newLine');
            if (this.editorCmDoc.getLine(this.currentLineNr).slice(0, -1) === this.originalCmDoc.getLine(this.currentLineNr)) {
              console.log('goLineDown');
              this.originalCm.execCommand('goLineDown');
              this.editorCode += '\n';
              this.$nextTick(() => {
                this.currentLineNr += 1;
                if (this.options.cm.autoIndent) {
                  const indentText = this.originalCmDoc.getLine(this.currentLineNr).match('[^\\S]*')[0];
                  this.editorCmDoc
                    .replaceRange(
                      indentText,
                      {
                        line: this.currentLineNr,
                        ch: 0,
                      },
                    );
                }

                this.editorCode += ' ';
                this.$nextTick(() => {
                  this.editorCm.execCommand('goDocEnd');
                  this.editorCm.execCommand('goColumnLeft');
                  console.timeEnd('newLine');
                });


                if (!this.stats.oneThirdTime && this.currentLineNr === Math.floor(this.fileInfo.lines / 3)) {
                  this.stats.oneThirdCharsCount = this.editorCode.length - 1;
                  this.stats.oneThirdTime = this.timeElapsed();
                } else if (!this.stats.lastThirdStartTime && this.currentLineNr === Math.floor(this.fileInfo.lines / 3 * 2)) {
                  this.stats.lastThirdCharsCount = this.originalCode.length - this.editorCode.length - 1;
                  this.stats.lastThirdStartTime = this.timeElapsed();
                }
              });
            }
          },
          Insert: () => {
            if (this.currentLineNr < this.fileInfo.lines) {
              this.stats.cheat = true;
              const text = `${this.originalCmDoc.getLine(this.currentLineNr)}\n `;
              // console.log(text);
              this.editorCmDoc.replaceRange(text, { line: this.currentLineNr, ch: 0 }, { line: this.currentLineNr + 1, ch: 0 });
              this.originalCm.execCommand('goLineDown');
              this.currentLineNr += 1;
              this.$nextTick(() => {
                this.editorCm.execCommand('goLineDown');
                this.editorCm.execCommand('goColumnBefore');
              });
            }
          },
          // disable keys
          Up: () => {},
          Down: () => {},
          Left: () => {},
          Right: () => {},
          Delete: () => {},
          Home: () => { this.completed(); },
          End: () => {
            this.editorCode = this.originalCode.slice(0, -1);
            this.completed(false);
          },
          PageUp: () => {},
          PageDown: () => {},
          'Ctrl-A': () => {},
        },
      };
    },
    tabWidth() {
      return this.code.tabSize ? this.code.tabSize : this.fileInfo.tabSize;
    },
  },
  methods: {
    onOriginalCmReady(cm) {
      cm.setOption('readOnly', true);
      this.originalCm = cm;
      this.originalCmDoc = cm.getDoc();
      console.log('originalCmReady ', Date.now());
      console.time('ready');
    },
    onEditorCmReady(cm) {
      this.editorCm = cm;
      this.editorCmDoc = cm.getDoc();
      console.log('editorCmReady ', Date.now());
      this.init();
    },
    onBeforeChange(_, change) {
      console.log(change);
      console.log(`change origin: ${change.origin}`);
      const o = change.origin;
      if (o === 'undo' || o === 'cut' || o === 'paste') {
        console.log('UNALLOWED EVENT');
        change.cancel();
        if (o === 'cut') {
          // codemirror lib edgecase
          this.editorCm.execCommand('goLineUp');
          this.originalCm.execCommand('goLineUp');
          this.editorCm.execCommand('goLineEnd');
        }
      } else if (change.origin !== 'setValue') {
        const from = {
          line: change.from.line,
          ch: change.from.ch,
        };

        const text = change.text[0];

        const to = {
          line: from.line,
          ch: from.ch + text.length,
        };

        const expectedText = this.originalCm.getRange(from, to);

        this.currentChange = {
          ...this.currentChange,
          time: this.timeElapsed(),
          position: {
            from,
            to,
          },
        };

        console.log(`change '${text}'`);
        if (text !== '' && this.started) {
          if (this.editorCmDoc.getLine(this.currentLineNr).length - 1 >= this.originalCmDoc.getLine(this.currentLineNr).length) {
            console.log(`cancel change end of line ${this.originalCmDoc.getLine(this.currentLineNr).length}`);
            this.currentChange.type = 'lineEnd';

            change.cancel();
          } else if (text !== expectedText) {
            console.warn(`'${text}' dont match '${expectedText}'`);

            this.toFix += text.length;

            this.currentTextMark = [
              from,
              to,
              { className: 'mark' },
            ];

            this.currentChange = {
              ...this.currentChange,
              type: 'mistake',
              fixQueuePos: this.toFix,
              expectedText,
            };
          } else if (this.toFix > 0) {
            // force user to correct previous mistakes
            console.log('fix mistakes first');
            this.currentChange.type = 'leftUnfixed';
            change.cancel();
          } else {
            this.currentChange.type = 'correct';
          }
        } else {
          this.currentChange.type = 'backspace';
        }
      }
    },
    async onCmCodeChange(_, [change]) {
      if (this.toFix > 0 && this.currentTextMark.length === 3) {
        console.warn(`NEW MARKER: line: ${this.currentTextMark[0].line}, from: ${this.currentTextMark[0].ch}, to: ${this.currentTextMark[1].ch}`);
        this.markers.unshift(this.editorCm.markText(...this.currentTextMark));
        this.currentTextMark = [];
      }
      if (!this.stats.firstCharTime) {
        this.stats.firstCharTime = Date.now();
      }

      if (this.currentChange.type === 'correct' || this.currentChange.type === 'mistake') {
        this.currentChange = {
          ...this.currentChange,
          ...this.currentKeyEv,
          text: change.text[0],
        };
      }

      this.stats.history.push(this.currentChange);
      this.currentChange = {};

      if (this.toFix === 0 && this.editorCode.length === this.originalCode.length - 1) {
        this.completed();
      }
    },
    async getCode() {
      if (!this.fileInfo.name) {
        console.log('USING CUSTOM CODE');
        return this.code.text;
      }
      const url = `http://localhost:3000/code/${this.language.name.replace('#', '_sharp')}/${this.fileInfo.name}.${this.language.ext}`;
      console.warn(url);
      try {
        const resp = await axios.get(url);
        return resp.data;
      } catch (err) {
        if (err.request) {
          throw new Error('No internet');
        } else {
          throw err;
        }
      }
    },
    start(interval) {
      clearInterval(interval);
      this.countdownText = 'START';
      this.started = true;
      this.editorCm.focus();
      console.log('START');
      this.stats.startTime = Date.now();
      const editor = document.querySelector('#editor');
      editor.addEventListener('focusout', (e) => {
        // becouse pointer-events: none
        console.log(e);
        if (e.relatedTarget !== null && e.relatedTarget.tagName !== 'INPUT' && e.relatedTarget.tagName !== 'BUTTON' && !this.isCompleted) {
          this.editorCm.focus();
          e.preventDefault();
        } else if (e.relatedTarget === null) {
          this.editorCm.focus();
          e.preventDefault();
        } else {
          this.started = false;
          this.countdown = 'Przerwae';
        }
      }, false);
      editor.addEventListener('keydown', (e) => {
        this.currentKeyEv = {
          keyCode: e.code,
          shift: e.shiftKey,
          alt: e.altKey,
        };
      });
    },
    init() {
      const initTime = Date.now();
      const interval = setInterval(() => {
        this.countdown -= 0.5;
        this.countdownText = Math.ceil(this.countdown);
        if (this.countdown === 2) {
          if (!this.editorsReady || !this.originalCode) {
            this.countdown += 0.5;
            console.warn(Date.now() - initTime);
            if ((Date.now() - initTime) / 1000 < 5) {
              this.countdownText = 'Chwileczk...';
            } else {
              console.warn('Long loading time');
              this.countdownText = 'Trwa do duej ni zwykle';
            }
          }
        } else if (this.countdown === 0) {
          this.editorCode += ' ';
          this.$nextTick(() => { this.editorCm.execCommand('goColumnLeft'); });
          this.start(interval);
        }
      }, 50);

      // for (let i = 1; i < this.fileInfo.lines; i += 1) {
      //   this.editorCode += '\n';
      // }
      console.log('loadMode ', Date.now());

      Promise.all([this.getCode(), loadTheme(this.options.cm.theme), loadMode(this.originalCm, this.language.mode)])
        .then((resp) => {
          console.log(resp);
          [this.originalCode] = resp;
          this.editorCm.setOption('mode', this.language.mode);
          this.editorsReady = true;
          console.timeEnd('ready');
        })
        .catch((err) => {
          clearInterval(interval);
          console.log(err);
          if (err.message === 'No internet') {
            this.countdownText = 'Nie masz internetu';
          } else {
            this.countdownText = 'Sprbuj ponownie pniej';
          }
        });
    },
    completed(currentStats = true) {
      console.warn('COMPLETED');
      this.editorCm.setOption('readOnly', true);
      this.editorCm.setOption('cursorBlinkRate', -1);

      if (currentStats) {
        this.stats = {
          ...this.stats,
          timeFromFirstInput: this.timeElapsed(),
          codeLength: this.editorCode.length,
          file: this.fileInfo,
        };
      } else {
        this.stats = stats;
      }


      console.warn(`TOTAL TIME: ${this.stats.timeFromFirstInput}`);
      console.log(JSON.parse(JSON.stringify(this.stats)));

      // this.$store.commit('SEND_STATS', this.stats);
      this.$router.replace('/results');
      this.$emit('completed', this.stats);
      console.log('completed class', this.timeElapsed());
      this.isCompleted = true;
      setTimeout(this.generateHeatMap, 60);
    },
    generateHeatMap() {
      const arr = this.stats.history;
      for (let i = 1; i < arr.length; i += 1) {
        let from;
        let to;
        if (arr[i].type === 'mistake') {
          // console.log(`Expected: ${arr[i].expectedText}`);
          from = arr[i].position.from;
          // for (let j = i + 1; j < arr.length; j += 1) {
          //   if (arr[j].type !== 'mistake') {
          //     to = arr[j - 1].position.to;
          //     i = j;
          //     break;
          //   }
          to = arr[i].position.to;
          // console.log(`mark start: ${from.line}:${from.ch}`);
          // console.log(`mark end: ${to.line}:${to.ch}`);
          this.originalCm.markText(from, to, { className: 'mark' });
        }

        // }
      }
      //   this.stats.history.forEach((ev) => {
      //   if (ev.type === 'mistake') {
      //     this.originalCm.markText(ev.position.from, ev.position.to, { className: 'mark' });
      //   }
      // });
      console.log('heatmap ready', this.timeElapsed());
      this.heatMapReady = true;
    },
    timeElapsed() {
      return Date.now() - this.stats.firstCharTime;
    },
  },
  mounted() {
    console.log('mounted');
  },
};
</script>

<style scoped>
.container {
  position: relative;
}

.info h4 {
  color: lightgray;
}

.code {
  opacity: 0;
  transition: opacity .5s ease-in;
  position: relative;
}

.ready {
  opacity: 1;
}

.code {
  /* pointer-events: none !important; */
}

.code >>> .CodeMirror-line {
  word-break: break-all;
}

#editor {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
}

#editor >>> .CodeMirror-linenumber {
  opacity: 0;
}

#original >>> .CodeMirror-line {
  z-index: 0;
  opacity: 0.6;
  will-change: filter;
  filter: saturate(50%);
  transition: opacity 2s, filter 2s;
}

.heatMap #original {
  opacity: 1;
}

.heatMap #original{
  filter: blur(9px);
}

.heatMap #editor >>> .CodeMirror-linenumber {
  opacity: 1;
}


.completed #original >>> .CodeMirror-code span {
  color: transparent !important;
}


#editor >>> .CodeMirror,#editor >>> .CodeMirror-gutters {
  background: transparent;
}

.code >>> .mark {
  background-color: rgba(255, 255, 255, .3);
}

.heatMap >>> .mark {
  background-color: rgba(11, 192, 238, 0.8) !important;
  outline: 7px solid rgba(195, 0, 255, 0.7);
}

.heatMap #original >>> .CodeMirror-linenumber {
  opacity: 0;
}
</style>
