<template>
  <nav>
    <div class="createRoom">
      <input type="text" placeholder="Nazwa" v-model="createRoomName">
      <button @click="createRoom" :disabled="createRoomName === ''">Utwrz pokj</button>
    </div>
    <div class="createRoomInfo pupUp" v-if="showCreateRoomInfo">
      <input type="text" placeholder="Nick" v-model="ownerName">
      <p>
        Jeste wacicielem pokoju {{ createRoomName }}.
        Wpisz swj nick, ktry bdzie widoczny dla innych graczy.

        Aby inni mogli doczy, udostpnij im ten link:
        <input type="text" disabled :value="`${origin}/join/${createRoomName}`">

        <button @click="closeCreateRoomInfo" :disabled="!ownerName">OK</button>
      </p>
    </div>
    <div class="joinRoom">
      <input type="text" placeholder="Nazwa" v-model="joinRoomName">
      <button :disabled="joinRoomName === ''">Docz</button>
    </div>

    <router-link v-if="this.$route.path !== '/about'" to="/about">O co tu chodzi?</router-link>
    <router-link v-if="this.$route.path !== '/contribute'" to="/contribute">Wspom</router-link>

    <label for="userLanguage">Angielski
      <input type="checkbox" id="userLanguage"/>
    </label>
    <a href="https://clone-it.github.io">Clone-It</a>

  </nav>
</template>

<script>
export default {
  name: 'NavBar',
  data() {
    return {
      createRoomName: '',
      joinRoomName: '',
      ownerName: '',
      showCreateRoomInfo: false,
      origin: window.location.origin,
    };
  },
  methods: {
    createRoom() {
      this.$store.commit('SET_ROOM_PROPERTY', ['name', this.createRoomName]);
      this.showCreateRoomInfo = true;
    },
    closeCreateRoomInfo() {
      this.$store.commit('SET_ROOM_PROPERTY', ['players', [
        {
          name: this.ownerName,
          owner: true,
        },
      ]]);
      this.showCreateRoomInfo = false;
    },
  },
};
</script>

<style scoped>
nav {
  display: flex;
  justify-content: space-between;
}

.createRoomInfo {
  position: absolute;
  z-index: 3;
  transform: translateY(50%);
  padding: 4em;
  margin: 3em;
  background: rgba(30, 30, 30, 0.9);
}

</style>
