<template>
  <div class="run">
    <div class="info">
      <h2>{{ language.name.replace('_', ' ') }}</h2>
      <h4 v-if="fileInfo.name">{{ fileInfo.name }}.{{language.ext }}</h4>
      <h4>{{ codeSource }}</h4>
    </div>
    <!-- Changing key remounts component -->
    <div class="main">
      <div class="code">
        <CodeEditor :key="componentKey" @reset="reset" @completed="completed" v-if="language.name" :fileInfo="fileInfo"/>
      </div>
      <transition>
        <Results v-if="$route.path === '/results' && stats" :stats="stats"/>
      </transition>
    </div>
  </div>
</template>

<script>
import CodeEditor from '@/components/CodeEditor.vue';
import Results from '@/views/Results.vue';
import { mapGetters } from 'vuex';

export default {
  name: 'Run',
  components: {
    CodeEditor,
    Results,
  },
  data() {
    return {
      componentKey: 1,
      stats: false,
    };
  },
  computed: {
    ...mapGetters(['language', 'code']),
    fileIndex() {
      if (this.code.text) {
        return -1;
      }
      return Math.floor(Math.random() * this.language.files.length);
    },
    fileInfo() {
      if (this.fileIndex !== -1) {
        return this.language.files[this.fileIndex];
      }
      return {
        lines: this.code.lines,
        tabSize: this.code.tabSize,
      };
    },
    codeSource() {
      if (this.fileInfo.name) {
        return this.fileInfo.source === 'own' ? 'Kod wasny' : this.fileInfo.source;
      }
      return 'Twj kod';
    },
  },
  methods: {
    reset() {
      this.componentKey += 1;
      if (this.$route.path === '/results') {
        this.$router.push('/run');
      }
    },
    completed(stats) {
      this.stats = stats;
    },
  },
  beforeMount() {
    if (!this.language.name) {
      this.$router.push('/');
    }
  },
};

</script>

<style scoped>
.main {
  display: flex;
  flex-wrap: wrap;
}

.code {
  max-width: 100%;
}

@media (max-width: 900) {
  .main {
    display: block;
  }
}

</style>
