const state = {
  userLanguage: 'polish',
  language: {},
  options: {},
};

const getters = {
  language: (state) => state.language,
  options: (state) => state.options,
};

const actions = {
};

const mutations = {
  SET_LANGUAGE: (state, languageObj) => {
    state.language = languageObj;
  },
  SET_OPTION: (state, payload) => {
    // eslint-disable-next-line prefer-destructuring
    state.options[payload[0]] = payload[1];
  },
  UPDATE_OPTIONS: (state, obj) => {
    state.options = {
      ...state.options,
      ...obj,
    };
  },
};

export default {
  state,
  getters,
  actions,
  mutations,
};
